.frame-if {
    height: 90%;
    width: 60%;
    position: absolute;
    top: 0%;
    left: 50%;
    -webkit-transform: translate(-50%,0%);
        -ms-transform: translate(-50%,0%);
            transform: translate(-50%,0%);
    background-color: white;
    border: 3px solid #453478;
    border-radius: 10px;
    -webkit-animation: showUp forwards 3s;
            animation: showUp forwards 3s;
    -webkit-transition: left 1s;
    -o-transition: left 1s;
    transition: left 1s;
    font-size: 1.2em;
    overflow-y: scroll;
}

.pull-left {
    left: -50%;
}

h2 {
    text-align: center;
    color: #453478;
}

.select-wrapper-if {
    margin: 5%;
}

.select-if {
    width: 60%;
    border-radius: 5px;
}

.select-if:focus {
    outline: none;
}

.number-input-if {
    margin: 5%;
}

.number-input-if input {
    width: 100%;
    border: 1px solid black;
    border-top: none;
    border-left: none;
    border-right: none;
}

.select-medicine-btn-if {
    height: 10%;
    margin-left: 3%;
    background-color: transparent;
    border: none;
}

.select-medicine-btn-if:hover {
    cursor: pointer;
}

.next-if {
    -webkit-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
}

.select-medicine-btn-if:hover .next-if {
    -webkit-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
            transform: rotate(360deg);
} 

.select-medicine-btn-if:focus {
    outline: none;
}



